import { defHttp } from '/@/utils/http/axios';
import type { SelectProps } from 'ant-design-vue';
import { ref } from 'vue';

enum Api {
  //巡检数据
  getInspectionUrl = '/da/group-operation/devops/inspection',
  //隐患缺陷
  getFaultAndHiddenUrl = '/da/group-operation/devops/fault-hidden',

  //工单统计
  getWorkOrderUrl = '/da/group-operation/devops/work-order',

  //顶端指标
  getAllIndexUrl = '/da/group-operation/devops/index',
  //损失电量
  getLossElecUrl = '/da/group-operation/devops/loss/elec',
  //两票
  getTicketcUrl = '/da/group-operation/devops/ticket',

  //安全统计
  getSecurityUrl = '/da/group-operation/devops/safety',
}
export const getInspectionData = (params) =>
  defHttp.post({ url: Api.getInspectionUrl, params }, {});

export const getFaultAndHiddenData = (params) =>
  defHttp.get({ url: Api.getFaultAndHiddenUrl, params }, {});

export const getWorkOrderData = (params) => defHttp.post({ url: Api.getWorkOrderUrl, params }, {});

export const getAllIndexData = () => defHttp.post({ url: Api.getAllIndexUrl }, {});

export const getLossElecData = () => defHttp.post({ url: Api.getLossElecUrl }, {});

export const getTicketcData = () => defHttp.post({ url: Api.getTicketcUrl }, {});

export const getSecurityData = (params) => defHttp.post({ url: Api.getSecurityUrl, params }, {});

